@font-face {
  font-family: 'EarlyFontDiary';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_220508@1.0/EarlyFontDiary.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: 'EarlyFontDiary', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 10px;
}

body{
  padding: 0 3rem;
}

p,span{
  font-size: 2rem;
}

input{
  font-size: 1.3rem;
  padding: 0.8rem 0.4rem;
}

button{
  font-size: 1.1rem;
  font-family: sans-serif;
  background-color: #f1f1f1;
  border: 1px solid #d9d9d9;
  padding: 3px 6px;
  border-radius: 9px;
}

button.submit {
  background-color: rgb(29, 170, 235);
  border: none;
  color: #fff;
  font-weight: 700;
  padding: 4px 8px;
}

.top_btnBox{
  display: flex;
  justify-content: flex-end;
}
.top_btnBox > button{
  margin-right: 0.7rem;
}
.top_btnBox > button:last-child{
  margin-right: 0;
}


header {
  /* background-color: aliceblue; */
  border-bottom: 2px solid rgb(136, 136, 136);
  padding: 2rem;
  margin-bottom: 4rem;
}
header h1{
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
}

.list {
  padding: 3rem 0;
  border-bottom: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.listName {
  color: deepskyblue;
  font-weight: 700;
}

.good>span {
  cursor: pointer;
}

.good{
  font-size: 1.7rem;
}

.date {
  width: 100%;
  font-size: 1.3rem;
  color: #555;
  margin: 1rem 0;
}

.Modal {
  background-color: #eee;
  padding: 2rem;
  position: absolute;
  top: 16.5rem;
  right: 0;
  width: 50%;
}
.Modal h4{
  font-size: 2rem;
}
.Modal p{
  font-size: 1.4rem;
}

.input {
  margin-top: 2rem;
}

.input>* {
  display: flex;
  flex-direction: column;
  margin-bottom: 5px;
}

.txt_box {
  width: 72%;
  height: 26rem;
}